.item {
  box-shadow: 0 2px 2px 1px rgba(0, 0, 0, 0.5);
  display: flex;
  background-color: white;
  padding: 10px 10px;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
}

.status {
  display: block;
  border-radius: 50%;
  width: 16px;
  height: 16px;
}

.name {
  font-size: 20px;
  font-weight: 500;
  text-transform: capitalize;
}
